.site-cost-calculator {
  color: #ffffff;
  background-color: $background--main;
}

.site-cost-calculator__inner-container {
  padding-right: 20px;
  padding-left: 20px;
}

.site-cost-calculator__heading {
  position: relative;

  margin-top: 0;

  font-size: 25px;
  font-weight: 700;
  line-height: 25px;

  // *** Стили для отображения иконки Калькулятора *** 
  &::after {
    content: "";

    position: absolute;
    top: 0;
    right: 0;

    display: block;

    width: 40px;
    height: 50px;

    background-color: $background--main;
    background-image: url("../icon-calculator.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}

.site-cost-calculator__total {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  width: 100%;
  min-height: 350px;

  padding-right: 20px;
  padding-left: 20px;

  background-color: $waydev-green;
}

.site-cost-calculator__total-title {
  font-size: 25px;
  font-weight: 700;
  line-height: 25px; 
}

.site-cost-calculator__total-cost-number,
.site-cost-calculator__total-cost-currency {
  font-size: 45px;
  font-weight: 700;
  line-height: 45px;
}

.site-cost-calculator__request-button {
  width: 100%;
  min-height: 80px;

  font-size: 20px;
  font-weight: 700;
  line-height: 20px;
  color: $white;

  background-color: transparent;

  border: 1px solid $white;
}